<template>
	<v-container>
		<v-layout row v-if="error">
			<v-flex xs12 sm6 offset-sm3>
				<app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
			</v-flex>
		</v-layout>
		<v-layout>
			<v-flex xs12 sm6 offset-sm3>
				<v-card>
					<v-card-text>
						<v-container>
							<form @submit.prevent="onSignIn">
								<v-layout row>
									<v-flex xs12>
										<v-text-field
										name="email"
										label="Email"
										v-model="email"
										type="email"
										required></v-text-field>
									</v-flex>
								</v-layout>
								<v-layout row>
									<v-flex xs12>
										<v-text-field
										name="password"
										label="Password"
										v-model="password"
										type="password"
										required></v-text-field>
									</v-flex>
								</v-layout>
								<v-layout row>
									<v-flex xs12>
										<v-btn type="submit" class="" :disabled="loading" :loading="loading">
											Sign In
										<span slot="loader" class="custom-loader">
									        <v-icon light>cached</v-icon>
									    </span>
									  </v-btn>
									</v-flex>
								</v-layout>
							</form>
						</v-container>
					</v-card-text>
				</v-card>
			</v-flex>
		</v-layout>
	</v-container>
</template>
<script>
export default {
	data () {
		return {
			email: '',
			password: ''
		}
	},
	computed: {
		user () {
			return this.$store.getters.user
		},
		error () {
			return this.$store.getters.error
		},
		loading () {
			return this.$store.getters.loading
		}
	},
	watch: {
		user (value) {
	        if (value !== null && value !== undefined) {
	          this.$router.push('/')
	        }
		}
	},
	methods: {
		onSignIn () {
			this.$store.dispatch('signUserIn', {email: this.email, password: this.password})
		},
		onDismissed () {
			// console.log('Dismissed Alert'),
			this.$store.dispatch('clearError')
		}
	}
}
</script>


