<template>
	<v-container>
		<v-layout row wrap v-if="loading">
			<v-flex xs12 class="text-xs-center">
				<v-progress-linear v-bind:indeterminate="true" color="primary"></v-progress-linear>
				<!-- <v-progress-circular 
					indeterminate 
					v-bind:size="50" 
					color="primary"
					:width="7"
          			:size="70"
					v-if="loading"></v-progress-circular> -->
			</v-flex>
		</v-layout>
		<v-layout row wrap v-else>
			<v-flex xs12>
				<v-card>
					<v-card-title>
						<h4 class="primary--text">{{ meetup.title }}</h4>
						<template v-if="userIsCreator">
							<v-spacer></v-spacer>
							<app-edit-meetup-details-dialog :meetup="meetup"></app-edit-meetup-details-dialog>
						</template>
					</v-card-title>
					<v-card-media :src="meetup.imageUrl" height="400px"></v-card-media>
					<v-card-text>
						<div class="secondary--text mb-2">{{ meetup.date | date }} - {{ meetup.location }}</div>
						<div>
							<app-edit-meetup-date-dialog :meetup="meetup" v-if="userIsCreator"></app-edit-meetup-date-dialog>
							<app-edit-meetup-time-dialog :meetup="meetup" v-if="userIsCreator"></app-edit-meetup-time-dialog>
						</div>
						<v-divider class="mb-1"></v-divider>
						<div>
							{{ meetup.description }}
						</div>
					</v-card-text>
					<v-card-actions>
						<v-spacer></v-spacer>
						<!-- <v-btn class="primary">Register</v-btn> -->
						<app-register-dialog 
						:meetupId="meetup.id"
						v-if="userIsAuthenticated && !userIsCreator"></app-register-dialog>
					</v-card-actions>
				</v-card>
			</v-flex>
		</v-layout>
	</v-container>
</template>
<script>
export default {
	props: ['id'],
	computed: {
	     loading () {
	      	return this.$store.getters.loading
	     },
		meetup () {
			return this.$store.getters.loadedMeetup(this.id)
		},
		userIsAuthenticated () {
			return this.$store.getters.user !== null && this.$store.getters.user !== undefined
		},
		userIsCreator () {
			if (!this.userIsAuthenticated) {
				return false
			}
			return this.$store.getters.user.id === this.meetup.creatorId
		}
	}
}
</script>


